  .nav_menu-btn span {
    width: 100%;
    height: 2px;
    background-color: var(--logo-text-color);
    border-radius: 4px;
    transition: all .2s;
    position: absolute;
    top: calc(50% - 1px);
    left: 0;
  }

  .nav_menu-btn span:first-child {
    top: 0;
  }

  .nav_menu-btn span:last-child {
    top: auto;
    bottom: 0;
  }

  .nav_menu-btn.active-burger span {
    transform: scale(0);
  }

  .nav_menu-btn.active-burger span:first-child {
    top: calc(50% - 1px);
    transform: rotate(-45deg);
